body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.login-form {
  align-items: center;
  height: 100vh;
}

.navbar {
  margin-bottom: 3rem;
}

.image-icon-box {
  position: relative;
  height: 100%;
  margin: 1rem;
  max-width: 120px;
  width: 100%;
}

.image-icon {
  height: 100%;
  max-width: 120px;
  margin: 1rem;
  width: 100%;
}

.delete {
  position: absolute;
  top: 0;
  right: 0;
}

.modal-content {
  width: auto;
}

.modal-content .is-child {
  padding: 2em 6em;
}
